.accountSettingsPageHeader {
    font-size: 3em;
    width: 100%;
    padding: 10px;
    padding-top: 50px;
    margin: 0px 0px 10px 0px;
    text-align: center;
    color: var(--cardtext);
  }

  .updatePasswordCard {
    margin: 100px auto;
    padding: 5px;
    background-color: var(--cardbg);
    color: var(--cardtext);
    box-shadow: 1px 0px 4px black;
    border-radius: 20px;
    width: 40%;
  }

  .updatePasswordCard h1 {
    text-align: center;
    padding: 10px;
    font-size: 2em;
  }

  .updatePasswordCard form {
    margin: 20px;
  }

  .updatePasswordCard input {
    width: 100%;
    box-sizing: border-box;
    outline: none;
    padding: 7px;
    border: none;
    border-bottom: 1px solid var(--cardtext);
    background: transparent;
    margin-bottom: 20px;
    font: 16px Arial, Helvetica, sans-serif;
    height: 45px;
    color: var(--cardtext)
  }

  .updatePasswordCard button {
    width: 50%;
    background-color: var(--cardtext);
    border: none;
    display: block;
    cursor: pointer;
    color: white;
    font-size: 16px;
    font-weight: 900;
    padding: 8px 18px;
    text-decoration: none;
    text-transform: uppercase;
    border-radius: 5%;
    margin: 10px auto;
  }

  .updatePasswordCard button:hover {
    background-color: var(--hover);
  }

  .updatePasswordCard .errorField {
    border-color: var(--error);
    margin-top: 10px;
  }
  .grantuploadCard .errorField::placeholder {
    color: var(--error);
  }
  
  .errorField {
    color: var(--error);
  }